<template>
	<div id="goods-list">
		<search-header :placeholder="$route.query.searchKey ? $route.query.searchKey : '选份礼物送给Ta' ">
			<router-link :to="{ name : 'Cart'}" slot="icon">
				<div class="cart">
					<img class="cart-icon" src="../../assets/cart-icon.png">
					<span class="num-icon">{{cartTotal?cartTotal:'0'}}</span>
				</div>
			</router-link>
		</search-header>
		
		<div class="tab-wrap">
			<rang-tab @sort='sort'></rang-tab>
		</div>
		
		<div class="goods-wrap" 
			v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="15"
      infinite-scroll-immediate-check="false">
			<good-list :goodList="goodList"></good-list>
		</div>
	</div>
</template>

<script>
	import GoodList from '@/components/ChooseGift/GoodList'
	import SearchHeader from '@/components/Header/SearchHeader'
	import RangTab from '@/components/RangTab/RangTab'
	
	export default {
		data() {
			return {
				cartTotal:0,
				tabIndex: 1,
				rang:'rang-icon-no',
				priceob : true,
				PARAMS : {
					a:1,
					type:1,
					pa:1,
					li:10,
					ob:1,
					spuId:'',
					promId:'',
					categoryId:'',
					orgId:'',
					Sk:'',
					lastId:''
				},

				goodList:[
//					{
//						skuId:1,
//						spuId:1,
//						type:1,
//						name:'商品名',
//						originalPrice:100,
//						discountPrice:50,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'加盟',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					},
//					{
//						skuId:2,
//						spuId:2,
//						type:1,
//						name:'酷美人 夜光石手链 男女款萤石手链手串手环饰品 男女款萤石手链手串手环饰品',
//						originalPrice:200,
//						discountPrice:100,
//						discount:5,
//						imagePath:'/static/img/pic.603b63b.png',
//						isDelete:2,
//						isUp:1,
//						tags:'自营',
//						isSoldOut:2,
//						promId:1,
//						commentCount:1,
//						praiseCount:100
//					}
				],
	      loading: false,
        loadingText: false,
			}
		},
		created(){
			this.paramsInit()
			this.getCartGoodsCount()			
//			this.getGoodsList()
//			this.getUserDetails()
		},
		methods: {
			//排序
			sort(arg){
				document.documentElement.scrollTop = 0
				this.PARAMS = Object.assign({},this.PARAMS,{
					pa:1,
					ob:arg,
					lastId:'',
				})
				console.log(this.PARAMS)
				this.getGoodsList()
				// this.goodList.push(obj);
			},
			
			
			paramsInit(){
				this.PARAMS = {
					a:this.$route.query.a?this.$route.query.a:1,
					type:this.$route.query.type?this.$route.query.type:1,
					pa:1,
					li:10,
					ob:1,
					spuId:this.$route.query.spuId?this.$route.query.spuId:'',
					promId:this.$route.query.promId?this.$route.query.promId:'',
					categoryId:this.$route.query.categoryId?this.$route.query.categoryId:'',
					orgId:this.$route.query.orgId?this.$route.query.orgId:'',
					Sk:this.$route.query.searchKey?this.$route.query.searchKey:'',
					lastId:'',
				}
				this.getGoodsList()
				
			},
			getUserDetails(){
				this.$Api.UserDetails(0).then((res)=>{
					console.log(res)
					this.cartTotal = res.q.user.cartGoodsTotal
				})
			},
			getCartGoodsCount(){
				this.$Api.getCartGoodsCount(1).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						this.cartTotal = res.q.count
					}
				})
			},
			
			getGoodsList(){
				Object.keys(this.PARAMS).forEach((key) => (this.PARAMS[key]=='') && delete this.PARAMS[key]);
				
				this.$Api.getGoodsList(this.PARAMS).then((res)=>{
					if(res.q.s == 0){
						if(this.PARAMS.pa == 1){
							this.goodList = res.q.goodses
						}else if(this.PARAMS.pa > 1 && res.q.goodses.length > 0){
							let gl = this.goodList
							this.goodList = gl.concat(res.q.goodses)
						}
					}
				})
			},
			loadMore() {
      	this.loading  = true;
				setTimeout(()=>{
					this.PARAMS = Object.assign({},this.PARAMS,{
						pa:parseInt(this.PARAMS.pa)+1,
					})
		      this.getGoodsList();
		      this.loading  = false;
				},1000)
	    },
		},
		components: {
			GoodList,
			SearchHeader,
			RangTab
		},

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#goods-list {
		/*margin-top:1.17rem;*/
		.tab-wrap{
			position: fixed;
			top: 1.17rem;
			width: 100%;
			z-index: 100;
			background: #fff;
		}
		.goods-wrap{
			position: absolute; 
			top: 1.07+1.17rem;
			width: 100%;
		}
	}
</style>


